import React, { lazy, Suspense } from 'react'
// import loadable from '@loadable/component'
import { Switch, Route, Redirect } from 'react-router'
import { createBrowserHistory } from 'history'
import config from '@/config/config'

import home from '@/views/home/home'
import Header from '@/components/header/Header'
import HeaderPromo from '@/components/header/HeaderPromo'
import Footer from '@/components/footer/Footer'
import PageContainer from '@/components/page_container/PageContainer'
import PageLoading from '@/components/loading/page_loading'

const Error404 = lazy(() => import('@/views/common/Error404'))

const routes = [
  {
    path: '/',
    redirect: '/home'
  },
  {
    path: '/home',
    Header,
    Main: home,
    Footer
  },
  {
    path: '/games',
    Header,
    Footer,
    Main: lazy(() => import('@/views/games/games'))
  },
  {
    path: '/promotions',
    isPromoHeader: true,
    Header: HeaderPromo,
    Main: lazy(() => import('@/views/promotions/promotions'))
  }
]
export const history = createBrowserHistory({
  basename: process.env.NODE_ENV !== 'development' ? config.outputDir : ''
})

export default function Routes() {
  return (
    <Switch>
      {routes.map((route, index) => {
        return (
          <Route
            key={index}
            path={route.path}
            exact={!route.exact}
            strict={route.strict}
            render={props => {
              return route.redirect ? <Redirect to={route.redirect}/>
                : <PageContainer route={route} routeProps={props}/>
            }}/>
        )
      })}
      <Suspense fallback={<PageLoading/>}>
        <Route path='*' component={Error404}/>
      </Suspense>
    </Switch>

  )
}

history.listen((location, action) => {
  // console.log(action, location)
  window.scrollTo(0, 0)
})
