import React, { useState, useEffect, useRef } from 'react'
import Swiper from 'swiper'
import PropTypes from 'prop-types'
import classNames from 'classnames/bind'
import { getUUId, useWebpImg } from '@/utils/util'
import { getWmsStatic } from '@/api/modules/wms'
import scss from './banner.module.scss'

const cx = classNames.bind(scss)

function areEqual(prevProps, nextProps) {
  return prevProps.field === nextProps.field
}

function Banner(props) {
  const { field } = props
  const bannerEl = useRef(null)
  const [bannerList, setBannerList] = useState([])
  const defaultImg = 'data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7'

  useEffect(getBannerList, [field])

  useEffect(initSwiper, [bannerList])

  function getBannerList() {
    const url = '/_banner/banner.txt'
    const params = {
      params: {
        _: getUUId()
      }
    }
    const _useWebpImg = useWebpImg
    getWmsStatic(url, params).then(({ data }) => {
      let bannerList = data[field].slider_data
      const wms = window.OMConfig.wmsServer + '/'
      bannerList = bannerList.filter(item => {
        item.imgUrl = `${wms}${item.imgurl}${_useWebpImg('_')}`
        return filterBannerList(item)
      })
      setBannerList(bannerList)
    })

    function filterBannerList(item) {
      let beginTime = item.begin_time
      let endTime = item.end_time
      const nowTime = Date.now()
      beginTime = beginTime && beginTime.replace(/-/g, '/')
      endTime = endTime && endTime.replace(/-/g, '/')
      beginTime = beginTime && new Date(beginTime).getTime()
      endTime = endTime && new Date(endTime).getTime()
      if (beginTime && endTime) {
        return (nowTime > beginTime && nowTime < endTime)
      } else if (beginTime) {
        return nowTime > beginTime
      } else if (endTime) {
        return nowTime < endTime
      }
      return true
    }
  }

  function initSwiper() {
    const swiperParams = {
      loop: true,
      lazy: {
        loadPrevNext: true
      },
      autoplay: {
        delay: 5000,
        disableOnInteraction: false
      },
      pagination: {
        el: '.swiper-pagination',
        clickable: true
      }
      // simulateTouch: false
    }
    const swiper = new Swiper(bannerEl.current, swiperParams)
    return () => swiper.destroy(true)
  }

  return (
    <div ref={bannerEl} className={cx('banner', 'default-bg', 'swiper-container')}>
      <div className={cx('swiper-wrapper')}>
        {bannerList.map((item, index) => {
          return (
            <div key={index.toString()} className={cx('banner__item', 'default-bg', 'swiper-slide')}>
              <img data-src={item.imgUrl} className={cx('banner__img', 'swiper-lazy')} src={defaultImg} alt={item.title}/>
            </div>
          )
        })}
      </div>
      <div className={cx('swiper-pagination')}/>
    </div>
  )
}

Banner.defaultProps = {
  field: 'home'
}

Banner.propTypes = {
  field: PropTypes.string.isRequired
}

export default React.memo(Banner, areEqual)
