import React, { Suspense, useEffect } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames/bind'
import { useSelector } from 'react-redux'
import { Redirect } from 'react-router'
import { imgLazyInit } from '@/utils/util'
import scss from './container.module.scss'
import PageLoading from '@/components/loading/page_loading'

const cx = classNames.bind(scss)

function PageContainer(props) {
  const loginStatus = useSelector(state => !!state.loginName)
  const showSideBar = useSelector(state => state.showSideBar)
  const { route, routeProps } = props
  const { Header, Main, Footer, auth, path, meta, isPromoHeader } = route

  const pageClassName = cx('page', {
    'padding-top-promo': Header && isPromoHeader,
    'padding-top-normal': Header && !isPromoHeader
  })
  const _Redirect = <Redirect to={{
    pathname: '/home',
    search: '?form=' + path,
    query: { form: path },
    state: {
      form: path
    }
  }}/>
  const Page = (
    <div className={pageClassName}>
      {Header && <Header meta={meta} {...routeProps}/>}
      <div className={cx('page__content', { 'open-side-bar': showSideBar })}>
        <Suspense fallback={<PageLoading/>}>
          {Main && <Main meta={meta} {...routeProps}/>}
        </Suspense>
        {Footer && <Footer meta={meta} {...routeProps}/>}
      </div>
    </div>
  )
  useEffect(() => {
    const observer = imgLazyInit()
    return () => observer && observer.disconnect()
  }, [])
  return auth && !loginStatus ? _Redirect : Page
}

PageContainer.propTypes = {
  route: PropTypes.object.isRequired,
  routeProps: PropTypes.object.isRequired
}

function areEqual(prevProps, nextProps) {
  return prevProps.route.path === nextProps.route.path
}

export default React.memo(PageContainer, areEqual)
